import CartActionTypes from "./cartTypes";

export const toggleCartHidden = () => ({
  type: CartActionTypes.TOGGLE_CART_HIDDEN,
});
export const toggleCartShow = () => ({
  type: CartActionTypes.TOGGLE_CART_SHOW,
});
//
export const addItem = (item) => ({
  type: CartActionTypes.ADD_ITEM,
  payload: item,
});

//
export const removeItem = (item) => ({
  type: CartActionTypes.REMOVE_ITEM,
  payload: item,
});
//couponCode  redux
export const addCouponCode = (couponCode) => ({
  type: CartActionTypes.ADD_COUPONCODE,
  payload: couponCode
})

export const clearCouponCode = () => ({
  type: CartActionTypes.CLEAR_COUPONCODE
})

//itemDetails
export const addQuantity = (item, quantity) => ({
  type: CartActionTypes.ADD_QUANTITY,
  payload: item,
  quantity,
});

////itemDetails
export const addItemToCart = (item) => ({
  type: CartActionTypes.ADD_ITEM,
  payload: item,
});

//checkout page
export const clearItemFromCart = (item) => ({
  type: CartActionTypes.CLEAR_ITEM_FROM_CART,
  payload: item,
});

//
export const clearCart = () => ({
  type: CartActionTypes.CLEAR_CART,
});
